import AppLoader from '../components/loader';
import { Button } from '../components/buttons';
import {Text} from '../components/text';
import { Badge } from '../components/badge';
import { Input } from '../components/input';
import { LoginForm } from '../components/LoginForm';
import { Navigator } from '../components/Navigator';
import { Divider } from '../components/divider';
import { GameTile } from '../components/GameTile';
import { AddGameModal } from '../components/addGameModal';
import { ParticipantTile } from '../components/participantTile';

import copyrightImage from '../images/copyright.png';
import { useNavigate } from 'react-router-dom';
export function Login() {
    const navigator = useNavigate();

    const navigateToDashboard = ()=>{
        navigator('/dashboard');
    }

  return (
    <div>
      <div class='login-background z-[-1] fixed top-0 left-0 block w-full h-full'></div>
      <div class='mx-auto mt-16 flex justify-center'>
      <LoginForm toDashboard={navigateToDashboard}></LoginForm>
      </div>
      <footer class='pb-4 pt-16 bottom-0 flex justify-center block w-full'>
        <div class='inline-flex justify-center items-center'>
          <img class="h-8" src={copyrightImage}/>
          <div class='inline-flex flex-col pl-1' style={{lineHeight: '16px'}}>
          <Text type='normal-white' value="2023"></Text>
          <Text type='small-white' value="copyright"></Text>
          </div>
          <div class='mx-2 flex items-center'><Divider height='30px' type='vertical' color='bg-white'></Divider></div>
          <Text type='normal-white' value="Mastercard"></Text>
        </div>

        </footer>

    </div>
  );
}