import { Component } from "react";
import { Text } from "./text";

export class OptionTile extends Component{
    constructor(props){
        super(props);
    }

    render(props){
        return <div class="text-center">
            <Text type='normal-secondary' value={this.props?.option}></Text>
            <Text type='normal' value={this.props?.value}></Text>
        </div>
    }
}