import { Component } from "react";
import { Divider } from "./divider";
import { OptionTile } from "./optionTile";
import { Text } from "./text";

export class QuestionTile extends Component{
    constructor(props){
        super(props);
    }

    render(){
        return <div class='grid grid-cols-12 px-6 py-6 bg-grey mb-2'>

        <div class="col-span-5 flex items-center">
        <div class="flex items-center pr-4">
            <Text type='small' value={this.props?.data.index}></Text>
            <span class='inline-block pl-2'><Divider type='vertical' height='30px'></Divider></span>
        </div>
         <Text type="normal" value={this.props?.data.question}></Text>
        </div>

        <div class='grid grid-cols-2 col-span-5'>
            <OptionTile value={this.props?.data.option1} option='option1'></OptionTile>
            <OptionTile value={this.props?.data.option2} option='option2'></OptionTile>
            <div class='pt-2'>
            <OptionTile value={this.props?.data.option3} option='option3'></OptionTile>
            </div>
            <div class='pt-2'>
            <OptionTile value={this.props?.data.option4} option='option4'></OptionTile>
            </div>
        </div>

        <div class='col-span-2 flex flex-col justify-center items-center'>
            <Text type='normal-secondary' value='Answer'></Text>
            <Text type='normal-action' value={this.props?.data.answer}></Text>
        </div>
        </div>
    }
}