import { Button } from '../components/buttons';
import {Text} from '../components/text';
import { Badge } from '../components/badge';
import { Navigator } from '../components/Navigator';
import { Divider } from '../components/divider';
import { GameTile } from '../components/GameTile';
import { AddGameModal } from '../components/addGameModal';
import { ParticipantTile } from '../components/participantTile';
import { useState, useRef, useEffect, useCallback } from 'react';

import copyrightImage from '../images/copyright.png';
import { AppLoader } from '../components/loader';
import { RestService } from '../components/RestService';
import { QuestionTile } from '../components/QuestionTile';


export function Dashboard(){
    const [showAddGameModal, setShowAddGameModal] = useState(false);
    const [showDashboard, setShowDashboard] = useState(false);
    const [showParticipants, setShowParticipants] = useState(true);
    const [switchselectedGame, setSwitchSelectedGame] = useState(true);

    

    const uid = useRef(null);
    const allGames = useRef(null);
    const selectedGame = useRef(0);

    const fetchedAllGamesOnInit = useRef(false);
    const fetchingAllGamesOnInit = useRef(false);

    //state management
    const HideAddGameModal =()=>{
        setShowAddGameModal(false)
    }

    const ShowAddGameModal =()=>{
        setShowAddGameModal(true)
    }
    const ShowParticipants =()=>{
        setShowParticipants(true)
    }

    const HideParticipants =()=>{
        setShowParticipants(false)
    }

    //api calls

    

    const fetchAllQuiz = useCallback(async()=>{
      let response = await RestService.getAllGames();

      if(response == null){
        setTimeout(fetchAllQuiz, 4000);
        return;
      }
      
      if(RestService.fieldCheck(response, 'error')){

      }

      if(RestService.fieldCheck(response,'success')){
        fetchedAllGamesOnInit.current = true;
        if(response.data.length > 0){
          allGames.current = response.data;
          setShowDashboard(true);
        }
      }
    }, []);

    const init = useEffect( ()=>{
      uid.current = localStorage.getItem('admin_uid');
      if(!fetchingAllGamesOnInit.current){
        fetchAllQuiz().catch(console.error);
        fetchingAllGamesOnInit.current = true;
      }
      
    }, [false]);

    return (<div className="App">
        <Navigator></Navigator>
         {/* this section is only visible when no game is available */}
         {!showDashboard &&
         <div class=" left-[50%] translate-x-[-50%] fixed top-[50%] translate-x-[-50%] w-full flex items-center flex-col">
          <div class='w-[50%] text-center pb-4'>
          <Text type="big" value="You've not yet add any games, please click the button below to a add game"></Text>
          </div>
          <span>
          <Button onClick={()=> ShowAddGameModal()} type={2} value='Add Game'></Button>
          </span>
         </div>
         }
         {/* End */}
        
        {/* Dashboard Content section */}
        {showDashboard &&
        <div>
        <div class="px-28 pt-16">
        <div class='flex col justify-between pb-6'>
          <Text value="Participants" type='big'></Text>
          <Button value='delete' type={3}></Button>
        </div>        
        <Divider></Divider>
        </div>
  
        <div class="pt-20 pb-20 flex justify-center">
          <div class='bg-white inline-flex flex-col' style={{width: '23%', height: '80vh'}}>
            <div style={{height: '38%'}}  class='game-banner w-100'>
              <div class='mt-5 flex p-4 items-center'>
                <span class="p-2">
                <Text type='big-white' value="Games"></Text>
                </span>
                <span>
                <Badge type='white' value="10"></Badge>
                </span>
              </div>
              <div class='bg-transparent-black p-4'>
              <Button onClick={()=> ShowAddGameModal()} value="Add Game"></Button>
              </div>
            </div>
  
            <div class="grow h-14">
              {allGames.current.map((game, index) => {
              return <GameTile key={index} data={game} selected={selectedGame.current == index} onClick={()=>{
                selectedGame.current = index;
                setSwitchSelectedGame(true);
              }}></GameTile>
              })}
            </div>
  
          </div>
          <div class='bg-white inline-flex flex-col ml-8 relative' style={{width: '58%', height: '80vh', }}>
          {/* Tab buttons section */}
            <div class="absolute top-0 translate-y-[-120%] flex justify-start">
            <Button onClick={()=>ShowParticipants()} value='Participants' type={showParticipants? 'action' : 5}></Button>
            
            <span class="inline-block px-3">
              <Button onClick={()=>HideParticipants()} value='Questions' type={showParticipants? 5: 'action'}></Button>
              </span>
            </div>
          {/* End */}
          {showParticipants && 
          <div class="flex-col h-full w-full absolute">
            {/* Participants placeholder section */}
          <div class="flex-col h-full w-full absolute">
  
          </div>
          {/* End */}
          {/* Participants content section */}
          <div class="flex-col h-full w-full absolute flex flex-col">
          <div class='flex items-center pt-6 px-14 pb-4'>
                      <Text type='big' value="Participants"></Text>
                      <div class='pl-4 relative' style={{top: '4px'}}><Badge value='100'></Badge></div>
                      </div>
                      <div class='px-8'><Divider></Divider></div>
                      <div class='px-14 pt-12'>
                      </div>
  
                      <div class='h-14 grow'>
                        <ParticipantTile data={{
                          user: {
                            fullname: 'ikponmoswa promise',
                            email: 'festusmaxwell7@gmail.com',
                            phonenumber: '0801232329383',
                          },
                          completed: true,
                          score: 2,
                          totalquestion: 4,
                          redeemtoken: '8DBS#@32'
                        }}></ParticipantTile>
                      </div>
          </div>
          
            {/* End */}
          </div>
          }
          {!showParticipants &&
          <div class="flex-col flex h-full w-full absolute">
            <div class='h-16 grow flex-col flex pb-2'>
                    <div class='flex flex-col items-center pt-6 px-8 pb-4'>
                    <div class='w-full'>
                      <div class='inline-block'><Text type='big' value="Questions"></Text></div>
                      <div class='pl-4 relative inline-block' style={{top: '4px'}}><Badge value={allGames.current[selectedGame.current].questions.length}></Badge></div>
                      <div class='py-4'><Divider></Divider></div>
                      </div>
                    </div>
                    <div class='px-8 overflow-y-scroll'>
                    {
                       allGames.current[selectedGame.current].questions.map(question=>{
                           return <QuestionTile key={question.index} data={question}></QuestionTile>
                        })
                    }
                    </div>
                </div>
          </div>
          }
          </div>
  
        </div>
        </div> }
        {/* End */}

        {/* This is modal component for adding a new game */}

        {
          showAddGameModal && <AddGameModal hideAddGameModalFunc={HideAddGameModal} fetchAllQuizFunc={fetchAllQuiz}></AddGameModal>
        }
        {/* End */}
    </div>);
  }