import { Component } from "react";

export class Divider extends Component{
    constructor(props){
        super(props);
    }

    render(){
        if(this.props?.type == 'vertical'){
            return <div class= {(this.props.color || ' bg-transparent-black') + ' inline-block'} style={{width: '1px', height: this.props?.height || '40px'}}>

            </div>
        }
        return <div class='bg-transparent-black' style={{height: '1px'}}></div>
    }
}