import { Component } from "react";

export class Text extends Component{
    constructor(props){
        super(props);
    }

    render(props){

        switch(this.props?.type){
            case 'normal':
                return <p class="text-normal text-primary-color">{this.props?.value}</p>
                break;
            case 'normal-bold':
                return <p class="text-normal text-primary-color style-bold">{this.props?.value}</p>
                break;
            case 'normal-white':
                return <p class="text-normal text-white">{this.props?.value}</p>
                break;
            case 'normal-action':
                return <p class="text-normal text-action-color">{this.props?.value}</p>
                break;
            case 'normal-secondary':
                return <p class="text-normal text-secondary-color">{this.props?.value}</p>
                break;
            case 'big':
                return <p class="text-big text-primary-color">{this.props?.value}</p>;
                break;
            case 'big-action':
                return <p class="text-big text-action-color">{this.props?.value}</p>;
                break;
            case 'big-white':
                return <p class="text-big text-white">{this.props?.value}</p>;
                break;
            case 'medium':
                return <p class="text-medium text-primary-color">{this.props?.value}</p>;
                break;
            case 'small':
                return <p class="text-small text-primary-color">{this.props?.value}</p>;
                break;
            case 'small':
                return <p class="text-small text-danger-color">{this.props?.value}</p>;
                break;
            case 'extra-small':
                return <p class="text-extra_small text-primary-color">{this.props?.value}</p>;
                break;
            case 'extra-small-bold':
                return <p class="font-bold text-extra_small text-primary-color">{this.props?.value}</p>;
                break;
            case 'extra-small-secondary':
                return <p class="text-extra_small text-secondary-color">{this.props?.value}</p>;
                break;
            case 'small-white':
                return <p class="text-small text-white">{this.props?.value}</p>;
                break;
            case 'smaller':
                return <p class="text-smaller text-primary-color">{this.props?.value}</p>;
                break;
            case 'smaller-secondary':
                return <p class="text-smaller text-secondary-color">{this.props?.value}</p>;
                break;
        }

        return <p class="text-normal">{this.props?.value}</p>
    }
}