import { Component } from "react";
import { AppLoader } from "./loader";
// import 

export class Button extends Component{
    constructor(props){
        super(props);
    }

    render(){
        var button = '';
        var postfixIcon = <span class><span class="icon-[mdi-light--home]"></span></span>;
        var loader = this.props?.showLoader? <AppLoader></AppLoader>: '';
    
        switch(this.props?.type){
            case 2:
               return  <div onClick={this.props?.onClick} class="transition-all cursor-pointer rounded-md py-3 inline-flex items-center px-3 hover:scale-[102%] radius bg-action-color text-white">
                {this.props?.value}
                    <div class="inline-flex h-30px w-30px justify-center items-center border-2 border-white rounded-md ml-2">
                        <span class="icon-[ph--plus-bold]"></span>
                    </div>
              </div>    
                break;
            case 3:
               return <div onClick={this.props?.onClick} class=" transition-all cursor-pointer rounded-md py-3 inline-flex items-center px-3 hover:scale-110 radius bg-secondary-danger-color text-danger-color border-danger-color border-2">
                {this.props?.value}
                    <div class="text-text-danger ml-2">
                        <span class="icon-[ph--trash-simple-fill]"></span>
                    </div>
              </div>    
                break;
            case 'action':
               return <div onClick={this.props?.onClick} class="transition-all cursor-pointer rounded-md py-3 inline-flex items-center px-3 hover:scale-110 radius bg-action-color text-white">
               {this.props?.value} {loader}
                   
             </div>    
                break;

            case 5:
               return  <div onClick={this.props?.onClick} class=" transition-all cursor-pointer rounded-md py-3 inline-flex items-center px-3 hover:scale-[102%] radius bg-secondary-action-color text-action-color border-action-color border-2">
               {this.props?.value}
             </div>    
                break;

            case 'big-secondary-action':
               return  <div onClick={this.props?.onClick} class=" transition-all cursor-pointer rounded-md py-3 inline-flex items-center px-3 hover:scale-[102%] hover:bg-action-color hover:text-white radius bg-secondary-action-color text-action-color border-action-color border-2">
               {this.props?.value}
             </div>    
                break;
                case 'long':
                    return <div onClick={this.props?.onClick} class="transition-all cursor-pointer flex justify-center items-center text-center rounded-lg py-3 text-big-medium items-center px-3 hover:scale-[102%] radius bg-action-color text-white">
                     {this.props?.value} {loader}
                   </div>    
                     break;
        }
        return <div onClick={this.props?.onClick} class="transition-all text-small cursor-pointer rounded-md py-2 inline-flex items-center px-2 hover:scale-110 radius bg-white text-action-color">
        {this.props?.value}
            <div class="inline-flex h-20px w-20px justify-center items-center border-2 border-action-color rounded-md ml-2">
                <span class="icon-[ph--plus-bold]"></span>
            </div>
      </div> ; 
    }
}