import { Component } from "react";
import { Duration } from "./Duration";
import { Text } from "./text";

export class GameTile extends Component{
    constructor(props){
        super(props);
    }

    toDuration(dateString){
        var date = new Date(dateString);
        console.log(date.getDate());
        var month = date.toLocaleDateString('default', { month: 'short' });

        return(`${date.getHours()}:${date.getMinutes()} ${month} ${date.getDate()} ${date.getFullYear()}`);
    }

    render(){
        var startDate = this.toDuration(this.props.data.start_date);
        var expiryDate = this.toDuration(this.props.data.expiry_date);
        
        // console.log(startDate);
        // console.log(`${startDate.getHours()} : ${startDate.getMinutes()} ${startDate.getMonth()} ${startDate.getDay()} ${startDate.getYear()}`);
        // console.log('2:30 may 25th 2023');

        return<div class={(this.props.selected? 'bg-action-color ': '') + "pb-2 pt-1"}>
            <div class="px-4 pb-1 pt-1 text-right">
                <Text value="2days ago" type='extra-small-secondary'></Text>
            </div>
            <div class="pl-4">
                <Text type={this.props.selected? 'normal-white': 'normal'} value={this.props.data.title}></Text>
            </div>
            <div class=" bg-transparent-black mt-2">
                <div class="pl-6 pt-2">
                <Text value="Expiry Date" type='extra-small-secondary'></Text>
                </div>
                <div class='flex items-center pl-6 pb-2'>
                <Text type='extra-small-bold' value={startDate}></Text>
                <div class='inline-block mx-4'><Duration type={this.props.selected? 'active': ''}></Duration></div>
                <Text type='extra-small-bold' value={expiryDate}></Text>
                </div>
            </div>
        </div>
    }
}