import { Component } from "react";
import durationImage from "../images/duration light.png";
import durationImageDark from "../images/duration dark.png";

export class Duration extends Component{
    constructor(props){
        super(props);
    }

    render(){
        if(this.props?.type == 'active'){
            return <img class="" src={durationImage}/>
        }
        return <img class="inline-block w-2"  src={durationImageDark}/>
    }
}