import { Component } from "react";
import { Button } from "./buttons";
import { Divider } from "./divider";
import { Text } from "./text";
import brandLogo from '../images/brandlogo.png';

export class Navigator extends Component{
    render(){
        return<div class='grid grid-cols-3 px-6 py-3 bg-white'>
             <div class=''></div>
             <div class='col-span-2 flex justify-between'>
                <div class="flex items-center">
                    <img class="inline-block mr-2 h-16" src={brandLogo}/>
                    <Divider height="38px" type='vertical'></Divider>
                    <div class="pl-6">
                    <Text text="normal" value={'maxwell'}></Text>
                    <Text text="normal" value="uptimax7@gmail.com"></Text>
                    </div>
                </div>
                <div>
                    <Button value="Sign out" type="action"></Button>
                </div>
             </div>
        </div>
    }
}

