import axios from "axios";

let baseUrl = 'http://localhost:8080';
export class RestService{
   static uid = localStorage.getItem('admin_uid');

   static async login(email, password){
        try{
            let response = await axios.post(baseUrl+'/admin/login', {
                email,
                password
            });
            return response.data;
        }catch(e){
            return RestService.fieldCheck(e?.response, 'data')? e.response.data : null;
        }
    }

   static async getQuizParticipants(quizId){
        try{
            let response = await axios.post(baseUrl+'/admin/get_quiz_participants', {
                quizId,
            });
            return response.data;
        }catch(e){
            return RestService.fieldCheck(e?.response, 'data')? e.response.data : null;
        }
    }

   static async redeemQuizByEmailAndToken(quizId, email, token){
        try{
            let response = await axios.post(baseUrl+'/admin/redeem_quiz_by_email_token', {
                quizId,
                email,
                token,
            });
            return response.data;
        }catch(e){
            return RestService.fieldCheck(e?.response, 'data')? e.response.data : null;
        }
    }

   static async getAllGames(quizId){
        try{
            let response = await axios.post(baseUrl+'/admin/get_all_quiz', {
                uid: RestService.uid
            });
            return response.data;

        }catch(e){
            return RestService.fieldCheck(e?.response, 'data')? e.response.data : null;
        }
    }

   static async createNewGame(gameData){
        gameData.uid = RestService.uid;
        try{
            let response = await axios.post(baseUrl+'/admin/create_new_game', gameData);
            return response.data;
        }catch(e){
            return RestService.fieldCheck(e?.response, 'data')? e.response.data : null;
        }
    }

   static async convertCsvToJson(csv){
       let formdata = new FormData();
       formdata.append('file', csv);

        try{
            let response = await axios.post(baseUrl+'/admin/convert_csv', formdata);
            console.log(response.data);
            return response.data;
        }catch(e){
            console.log(e);
            return RestService.fieldCheck(e?.response, 'data')? e.response.data : null;
        }
    }

    static fieldCheck(data, fieldname){
        return Object.keys(data).includes(fieldname);
    }
}

