import { Component } from "react";
import { Text } from "./text";

export class Input extends Component{
    constructor(props){
        super(props);
    }

    render(props){
        let formField = <input onChange={(e)=>this.props?.onChange(e.target.value)} placeholder={this.props?.placeholder} type={this.props?.type} class="text-normal block w-100 text-primary-color p-2 border-2 rounded-md border-white bg-form-white"/>;
        switch(this.props?.type){
            case 'password':
               return formField;
               break
            case 'email':
               return formField;
               break
            case 'date':
                return <div class="inline-block w-30 p-2 border-2 rounded-md border-overlay-grey bg-white">
                    <Text type="smaller-secondary" value={this.props?.title}></Text>
                    <input onChange={(e)=>this.props?.onChange(e.target.value)} placeholder={this.props?.placeholder} type='datetime-local' class="text-normal text-primary-color block"/>
                </div>
            default:
               return <input onChange={(e)=>this.props?.onChange(e.target.value)} placeholder={this.props?.placeholder} type={this.props?.type} class="text-normal w-80px text-primary-color p-2 inline-block w-30 h-22 p-2 border-2 rounded-md border-overlay-grey bg-white"/>
        }
    }
}