import { Component, React } from 'react';
import { Button } from './buttons';
import { Input } from './input';
import { Text } from './text';
import { isEmail, isPassword } from './validator';
import { RestService } from './RestService';
import { useNavigate } from 'react-router-dom';

export class LoginForm extends Component{

    constructor(props){
        super(props);

        this.state = {
            email: '',
            password: '',
            emailError: null,
            passwordError: null,
            fetchingData: false
        }
    }

    updateFieldValue = (field, value)=>{
        console.log(value);
        this.setState((state)=>{
            return{
                ...state,
                [field]: value
            }
        })
    }

    onEmailChange = (value)=>{
        console.log(value);
       this.updateFieldValue('emailError', isEmail(value)? null: 'Invalid email address');
       this.updateFieldValue('email',  value);
    }

    onPasswordChange = (value)=>{
        this.updateFieldValue('passwordError', isPassword(value)? null: 'Password input is invalid');
        this.updateFieldValue('password', value);
    }

    async submit(){
        this.onEmailChange(this.state.email);
        this.onPasswordChange(this.state.password);
        if(this.state.fetchingData) return;
        if(this.state.emailError != null && this.state.passwordError != null) return;

        this.updateFieldValue('fetchingData', true);
        let response = await RestService.login(this.state.email, this.state.password);
        
        this.updateFieldValue('fetchingData', false);
        if(response == null){
            return;
        }

        if(RestService.fieldCheck(response, 'error')){
            console.log(response.error);
            if(RestService.fieldCheck(response?.error, 'email')){
                this.updateFieldValue('emailError', response.error.email);
            }

            if(RestService.fieldCheck(response?.error, 'password')){
                this.updateFieldValue('passwordError', response.error.password);
            }

            if(RestService.fieldCheck(response?.error, 'invalid_user')){
                this.updateFieldValue('emailError', 'This email is not a registered admin');
            }

            if(RestService.fieldCheck(response.error, 'incorrect_password')){
                this.updateFieldValue('passwordError', 'Your is password incorrect');
            }
            return;
        }

        if(RestService.fieldCheck(response,'success')){
            localStorage.setItem('admin_uid', response.data.uid);
            localStorage.setItem('admin_username', response.data.uid);
            localStorage.setItem('admin_phonenumber', response.data.phonenumber);
            this.props?.toDashboard();
        }


    }

    render(){
        return <form class='bg-secondary w-[30vw] px-8 py-8 bg-white-78 rounded-lg' onSubmit={this.submit}>
                <center><b><Text value="Admin Login" type="big"></Text></b></center>
                <center class="pt-2 px-10"><Text type="normal-secondary" value="Please provide your admin details to sign into an admin account"></Text></center>
                <div class='pt-10'><Text type='small' value='Admin Email'></Text></div>
                <Input type='email' placeholder="Email" onChange={this.onEmailChange}></Input>

                {this.state.emailError != null && <div class='pt-3'><Text type='small-danger' value={this.state.emailError}></Text></div> }

                <div class='pt-3'><Text type='small' value='Admin Password'></Text></div>
                <Input type='password' placeholder="Password" onChange={this.onPasswordChange}></Input>

                {this.state.passwordError != null && <div class='pt-3'><Text type='small-danger' value={this.state.passwordError}></Text></div> }

                <div class='pt-4 pb-4'><p class='text-normal text-secondary-color'>This is panel is only available to the <b>admin</b>, for access, meet the <b>admin</b></p></div>
                <b><Button type="long" onClick={(e)=>this.submit.bind(this)()} showLoader={this.state.fetchingData} value="Log in"></Button></b>
             </form>
    }
}