import { Component } from "react";
import userPlaceholderImage from '../images/participant.png'
import contactImage from '../images/contact.png'
import notCompletedImage from '../images/not completed.png'
import { Text } from "./text";
import { Button } from "./buttons";

export class ParticipantTile extends Component{
    constructor(props){
        super(props);
    }

    render(){
        return <div class='bg-grey p-4 grid grid-cols-12 mx-6'>
            <div class='col-span-6 flex'>
                <img class='inline-block h-16' src={userPlaceholderImage}/>
                <span class="inline-block pl-2 pt-2">
                    <Text type='normal' value={this.props?.data.user.fullname}></Text>
                    <Text type='normal-secondary' value={this.props?.data.user.email}></Text>
                    <div class="bg-gray rounded-sm flex items-center mt-2">
                        <img class='h-8' src={contactImage}/>
                        <span  className="pl-2">
                            <Text type='normal' value={this.props?.data.user.phonenumber}></Text>
                        </span>
                    </div>
                </span>
            </div>
            <div class='col-span-3 flex'>
                <div class='w-full flex justify-start pt-2 flex-col items-center'>
                        <Text type='normal-secondary' value='Score'></Text>
                        <Text type='normal' value={this.props?.data.score + "/" + this.props?.data.totalquestion}></Text>
                </div>
                <div class='w-full text-center flex justify-start pt-2 flex-col items-center'>
                <Text type='normal-secondary' value='Completed'></Text>
                <img class='inline-block' src={notCompletedImage}/>
                </div>
            </div>

            <div class='col-span-3 flex justify-end pr-4'>
                <div class='flex justify-center flex-col'>
                    <Text type='normal-bold' value={this.props?.data.redeemtoken}></Text>
                    <span>
                    <Button type='big-secondary-action' value='Redeem'></Button>
                    </span>
                </div>
            </div>
        </div>
    }
}