import { type } from "@testing-library/user-event/dist/type";
import { Component } from "react";
import { Text } from "./text"

export class Badge extends Component{
    constructor(props){
        super(props);
    }

    render(props){
        switch(this.props?.type){
            case 'white':
                return  <div class="inline-flex px-2 justify-center items-center border-2 border-white"><Text type='normal-white' value={this.props?.value}></Text></div>
                break;
                default:
                return <div class='inline-block bg-secondary-action-color px-2'><Text type='normal-action' value={this.props?.value}></Text></div>
        }
    }
}