import { Component } from "react";
import { Input } from "./input";
import { Text } from "./text";
import  dragCsvImage  from "../images/drag drop.png";
import { QuestionTile } from "./QuestionTile";
import { Badge } from "./badge";
import { Divider } from "./divider";
import { Button } from "./buttons";
import { RestService } from "./RestService";

export class AddGameModal extends Component{
    constructor(props){
        super(props);
        this.state ={
            showModal: true,
            showPreview: false,
            gameTitle: '',
            startDate: null,
            expiryDate: null,
            requestingCsvPreview: false,
            questions: null,
            creatingNewGame: false,
            csvFile: null
        }
    }

    setGameTitle(value){
        this.setState(prevState=>{
            return{
                ...prevState,
                gameTitle: value
            }
        })
    }


    switchState(state, value){
        this.setState(prevState=>{
            return{
                ...prevState,
                [state]: value
            }
        })
    }

    switchStates(value){
        this.setState(prevState=>{
            return{
                ...prevState,
                ...value
            }
        })
    }

    setDateState(type,date){
        this.setState(prevState=>{
            return{
                ...prevState,
                [type]: date
            }
        })
    }

    setStartDate(date){
        console.log(date);
        this.setDateState('startDate', date);
    }

    setExpiryDate(date){
        this.setDateState('expiryDate', date);
    }

    hidePreview(){
        this.switchPreview(false);
    }

    async showPreview(){
        if(this.state.gameTitle == '') return;
          if(this.state.requestingCsvPreview) return;
          if(this.state.csvFile == null) return;


      this.switchState('requestingCsvPreview', true);
      let response = await RestService.convertCsvToJson(this.state.csvFile);
      if(response == null){
        console.log('something happened');
        return;
      }

      if(RestService.fieldCheck(response, 'error')){
        return;
      }

      this.switchStates({showPreview: true, questions: response.data});

    //   this.switchPreview(true);
    }

    async createNewGame(){
        this.switchState('creatingNewGame', true);
        let response = await RestService.createNewGame({
            title: this.state.gameTitle,
            start_date: this.state.startDate,
            expiry_date: this.state.expiryDate,
            questions: this.state.questions
        });

      if(response == null){
        console.log('something happened');
        return;
      }

      if(RestService.fieldCheck(response, 'error')){
        return;
      }

      if(RestService.fieldCheck(response, 'success')){
        this.props.fetchAllQuizFunc();
        this.props.hideAddGameModalFunc();
      }

    }

    switchPreview(state){
        this.setState((prevState)=>{
            return {
                ...prevState,
                showPreview: state,
            }
        })
    }


    hideModal(){
        this.switchModal(false);
    }

    showModal(){
        this.switchModal(true)
    }

    switchModal(state){
        this.setState((prevState)=>{
            return {
                ...prevState,
                showModal: state,
            }
        })
    }

    onCsvDrop(e){
        e.preventDefault();
       let file = e.dataTransfer.files[0];
       this.switchState('csvFile', file);
       let fileArray = [...e.dataTransfer.files];
        console.log(file);
    }

    preventDefault(e){
        e.preventDefault();
    }

    render(){
        return <div class='fixed'>
        <div class='fixed top-0 bg-transparent-black w-full h-[100vh] block'>
        </div>
        <div class='fixed bg-white flex flex-col top-[50%] left-[50%] translate-x-[-50%] translate-y-[-50%]' style={{height: '80vh', width: '70vw'}}>
               <div class="bg-secondary-action-color">
                <div class="flex justify-between items-center bg-secondary-action-color p-2">
                    <Text type='normal' value="Add Game"></Text>
                    <span onClick={()=>this.props?.hideAddGameModalFunc()} class="cursor-pointer icon-[ph--x-bold]"></span>
                </div>
                <div class="pt-6 pl-10 relative transition-all">
                    <div class='absolute top-0 p-6 inline-flex justify-end right-0'>
                        {!this.state.showPreview && <Button onClick={e=> this.showPreview.bind(this)()} type='action' showLoader={this.state.requestingCsvPreview}  value='Preview'></Button>}
                        
                        {this.state.showPreview && <div>
                        <Button  type='action' value='Preview'></Button>
                        <span class='inline-block mx-2'><Button type='action' value='Save' onClick={()=>this.createNewGame.bind(this)()} showLoader={this.state.creatingNewGame}></Button></span>
                        </div>}
                    </div>
                    {this.state.showPreview && <Text type='big-white' value={this.state.gameTitle}></Text> }
                    
                    {!this.state.showPreview &&
                    <div>
                        <Text type='normal' value="Enter a game title, and select the select a path to the questions csv file"></Text>
                        <div class="py-2">
                            <Input placeholder="Game Title" onChange={this.setGameTitle.bind(this)}></Input>
                            <br/>
                            <div class='inline-block pt-2 mr-2'>
                            <Input title='start Date' onChange={this.setStartDate.bind(this)}  type='date'></Input>
                            </div>
                            <div class='inline-block pt-2'>
                            <Input title='Expiry Date' onChange={this.setExpiryDate.bind(this)} context={this} type='date'></Input>
                            </div>
                        </div>
                    </div>
                    }
                </div>                
                </div> 
                
                {/* Drag And Drop Area */}
                {!this.state.showPreview &&
                <div class='h-16 grow flex relative'>
                    <div class="h-full w-full flex items-center justify-center relative">
                        <img class='h-48' src={dragCsvImage}/>
                        <div onDragOver={this.preventDefault} onDragLeave={this.preventDefault} onDragEnter={this.preventDefault}  onDrop={e=> this.onCsvDrop.bind(this)(e)} class='w-full absolute top-[50%] translate-y-[-50%] text-center'>
                            <Text type="big-action" value="Drag CSV Here"></Text>
                        </div>
                    </div>
                </div>}
                {/* End */}
                {this.state.showPreview &&
                <div class='h-16 grow overflow-y-scroll'>
                    <div class='flex items-center pt-6 px-14 pb-4'>
                    <Text type='big' value="Questions"></Text>
                    <div class='pl-4 relative' style={{top: '4px'}}><Badge value='100'></Badge></div>
                    </div>
                    <div class='px-8'><Divider></Divider></div>
                    <div class='px-14 pt-12'>
                    {
                       this.state.questions.map(question=>{
                           return <QuestionTile key={question.index} data={question}></QuestionTile>
                        })
                    }
                    
                    </div>
                </div>  }
        </div>
        </div>
    }
}